.header-top {
  color: #ffffff;
  background: #0085dd;
  padding: 22px 0;
  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    li:nth-child(even) {
      position: relative;
      display: flex;
      align-items: center;
      &:after {
        content: '';
        display: inline-block;
        width: 5px;
        height: 5px;
        background: #ffffff;
        border-radius: 50%;
        opacity: 0.3;
      }
    }
  }
  &__link {
    font-size: 20px;
    line-height: 130%;
    a:hover {
      opacity: 0.7;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-top__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      font-size: 20px;
      line-height: 130%;
      cursor: pointer;
      transition: all 0.3s;
      i {
        flex-shrink: 0;
        transform: rotate(-180deg);
        width: 10px;
        display: flex;
        align-items: center;
        margin-left: 5px;
      }
      i svg {
        width: 100%;
        height: auto;
      }
      &:hover {
        opacity: 0.7;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      width: 250px;
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      background: #0085dd;
      padding: 20px 10px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-link {
      width: 100%;
      font-size: 20px;
      line-height: 130%;
      &:hover {
        opacity: 0.7;
      }
    }
  }
}
.header-bottom {
  background: #ffffff;
  padding: 32px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    flex-grow: 1;
    margin-left: 70px;
    gap: 50px;
  }
  &__logo {
    display: block;
    width: 375px;
    img {
      width: 100%;
    }
  }
  &__right {
    display: flex;
    justify-content: space-between;
  }
  &__wrap {
    display: flex;
    gap: 50px;
  }
  &__subtitle {
    font-size: 18.8px;
    line-height: 130%;
    font-weight: 700;
    color: #232323;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 25px;
  }
  &__item {
    display: flex;
    align-items: center;
    font-size: 17px;
    line-height: 130%;
    color: #232323;
    i,
    .phone-icon {
      flex-shrink: 0;
      width: 17px;
      margin-right: 15px;
      img {
        width: 100%;
      }
    }
    span {
      transition: all 0.3s;
    }
  }
  .location,
  .email {
    &:hover {
      span {
        color: #0084ff;;
      }
    }
  }
  &__phone {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
    a:hover {
      color: #0084ff;
    }
  }
  &__btns {
    width: 100%;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
  }
  &__btn {
    width: 100%;
    height: 45px;
    span {
      font-size: 14.5px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__social {
    width: 40px;
    img {
      width: 100%;
    }
  }
}
.burger {
  display: none;
  position: relative;
  z-index: 12;
  width: 34px;
  height: 26px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #288eca;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 12px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 11px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1300px) {
  .header-bottom {
    &__logo {
      width: 280px;
    }
    &__right {
      gap: 30px;
      margin-left: 50px;
    }
    &__wrap {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-top {
    padding: 18px 0;
    ul {
      li:nth-child(even) {
        &:after {
          width: 3px;
          height: 3px;
        }
      }
    }
    &__link {
      font-size: 15px;
    }
    &__item {
      &-show {
        font-size: 15px;
      }
      &-link {
        font-size: 15px;
      }
    }
  }
  .header-bottom {
    padding: 22px 0;
    &__subtitle {
      font-size: 14px;
    }
    &__items {
      margin-top: 20px;
    }
    &__item {
      font-size: 13px;
      i,
      .phone-icon {
        width: 14px;
      }
    }
    &__btns {
      max-width: 225px;
    }
    &__btn {
      height: 32px;
      span {
        font-size: 11px;
      }
    }
    &__social {
      width: 32px;
    }
  }
}

@media screen and (max-width: 1160px) {
  .header-bottom {
    &__logo {
      width: 230px;
    }
  }
}

@media screen and (max-width: 960px) {
  .header-top {
    &__link {
      font-size: 12px;
    }
    &__item {
      &-show {
        font-size: 12px;
        i {
          width: 7px;
        }
      }
      &-hidden {
        width: 200px;
      }
      &-link {
        font-size: 12px;
      }
    }
  }
  .header-bottom {
    &__right {
      flex-grow: unset;
    }
    .header-bottom__group:nth-child(1) ~ .header-bottom__group {
      display: none;
    }
  }
}

@media screen and (max-width: 740px) {
  .header-top {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    padding: 100px 15px 50px;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    ul {
      flex-direction: column;
      text-align: center;
      gap: 15px;
      li:nth-child(even) {
        display: none;
      }
    }
    &__link {
      font-size: 14px;
    }
    &__item {
      &--active {
        .header-top__item-hidden {
          display: grid;
        }
      }
      &-show {
        justify-content: center;
        font-size: 14px;
      }
      &-hidden {
        display: none;
        position: static;
        background: transparent;
        padding: 15px 0 0 0;
        opacity: 1;
        pointer-events: all;
      }
      &-link {
        font-size: 14px;
      }
    }
  }
  .header-bottom {
    padding: 0;
    .container {
      padding: 0;
    }
    &__left {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 20px;
    }
    &__right {
      gap: 15px;
      background: #0085dd;
      padding: 10px 20px;
      margin-left: 0;
    }
    &__inner {
      display: block;
    }
    &__logo {
      width: 200px;
    }
    &__wrap {
      flex-shrink: 0;
    }
    &__subtitle {
      display: none;
    }
    &__items {
      margin: 0;
    }
    &__item {
      font-size: 14px;
    }
    .location,
    .email {
      display: none;
    }
    &__phone {
      flex-direction: column;
      color: #ffffff;
      a {
        position: relative;
        padding-left: 22px;
      }
      a:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 15px;
        height: 15px;
        background-image: url("../img/phone2.webp");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
    .phone-icon {
      display: none;
    }
    &__btn {
      display: none;
      sapn {
        font-size: 14px;
      }
    }
    .btn-blue {
      display: flex;
      background-color: #ff5700;
      background-image: linear-gradient(90.0deg, #cc1300 -24%, #ff8a00 125%);
      &:hover {
        background-image: linear-gradient(90.0deg, #ff8a00 -24%, #cc1300 125%);
      }
    }
    &__socials {
      display: none;
    }
  }
  .burger {
    display: block;
  }
}