*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  box-sizing: border-box;
}

body,
div,
p,
h1,
h2,
h3,
h4,
h5,
ul,
li,
a,
form,
label {
  margin: 0;
  padding: 0;
  border: 0;
  text-decoration: none;
  line-height: 1;
  box-sizing: border-box;
}

button,
input,
textarea {
  outline: none !important;
}

button {
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: all 0.5s;
}

input[type=text]:focus,
input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus,
input:active {
  outline: none !important;
}

html {
  height: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  height: 100%;
  overflow-x: hidden;
}
.fixed-body {
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
img {
  margin: 0;
  padding: 0;
  height: auto;
  object-fit: cover;
}

a {
  color: inherit;
  text-decoration: none;
  transition: 0.3s;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1590px;
}

@media screen and (max-width: 960px) {
  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 400px) {
  .container {
    padding: 0 15px;
  }
}
